<template>
  <div id="app">
    <Header></Header>
    <div class="bg"></div>
    <el-row class="main-row" :gutter="10" >
      <el-col :span="12" :offset="4"><div class="main"><router-view/></div></el-col>
      <el-col :span="4" ><Sidebar></Sidebar></el-col>
    </el-row>
    
  
  </div>
</template>

<script>
import Header from './components/Header'
import Sidebar from './components/Sidebar'
export default {
  name: 'App',
  components:{
    Header,
    Sidebar,
  },
  data() {
    return {
      content: ''
    }
  },
}

</script>

<style>
body,head,#app{margin:0;padding:0;}
a{text-decoration:none; color: #2c3e50;}

#app {
    font-family: Avenir,Helvetica,Arial,sans-serif;;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    width: 100%;
    overflow: hidden;
    font-size: 14px;
  }
    .bg{ 
      width: 100%; 
      height: 600px;
      background: url("/static/bg.jpg") no-repeat fixed;
      background-position: center -350px;
      background-size: 100%;
      z-index: -1;
      position: relative;
      }
.main-row{ -index: 1; background: #efefef; padding-top: 10px;}
.main{ width:calc(100% - 30px); padding:0 15px 15px; box-sizing: content-box;  margin-top:15px; min-height: 900px;}
</style>



