<template>
  <div class="header">
    <el-menu default-active="0" class="el-menu-demo" mode="horizontal"
      background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
      <el-menu-item index="0">
        <router-link :to="'/'"></router-link>
      </el-menu-item>
      <el-menu-item :index="item.id" v-for="item in info" :key="item.id">
        <router-link :to="'/Category/'+item.id">{{ item.category_name }}</router-link>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
  export default {
    name: 'Header',
    data() {
      return {
        info: null
      }
    },
    created: async function () {
      const {
        data: res
      } = await this.$http.get("Category/list");
      this.info = res;
    },
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header {
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 10000;
  }

  .el-menu.el-menu--horizontal {
    border: none;
    position: relative;
  }

  a {
    display: block;
    padding: 0 20px;
  }

  li {
    padding: 0;
  }

</style>



