<template>
  <div class="MyCard">
    <el-card class="box-card">
    <div slot="header" class="clearfix">
        <i class="el-icon-user-solid"></i><span></span>
    </div>
    <ul>
        <li v-for="item in info" :key="item.title" class="text item">
            {{item.title}} : {{item.content}}
        </li>
    </ul>
    <div>
    </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'MyCard',
  data () {
    return {
      info:[{title:"Name",content:"gyingh"},{title:"Motto",content:" "},{title:"Email",content:"gyingh@outlook.com"}]
    }
  }
}
</script>

<style scoped>
ul{
    padding-left:0;
    margin: 0;
}
li{
    list-style-type:none;
    line-height: 35px;
}
  
</style>



