<template>
  <div class="hello">
    {{ title }}
    <div class="Markdown" v-html="content"></div>
  </div>
</template>

<script>
import '@/assets/css/markdown.css';
export default {
  name: 'HelloWorld',
  data () {
    return {
      title:"hello world!",
      content: 'Welcome to Your Vue.js App'
    }
  },
  mounted:async function(){
    const {data:res} = await this.$http.get();
    this.content = this.$Markdown.render(res.content);
    this.title=res.title;
  }
}
</script>

<style>
  .hello{ background: #fff; width:calc(100% - 20px); padding: 10px; overflow: hidden;}
  
</style>



