<template>
  <div class="Article">
    <h1 class="title">{{ info.title }}</h1>
    <div class="meta">
      <span class="meta-item">
        <span class="meta-item-icon">
          <i class="el-icon el-icon-date"></i>
        </span>
        <span class="meta-item-text"> {{ info.date }}</span>
      </span>
      <span class="meta-item">
        <span class="meta-item-icon">
          <i class="el-icon el-icon-folder"></i>
        </span>
        <span class="meta-item-text"> <router-link :to="'/Category/'+info.category_id">
            <el-link type="primary">{{ info.category_name }}</el-link>
          </router-link></span>
      </span>
    </div>
    <div class="Markdown" v-html="info.content"></div>
  </div>
</template>

<script>
  export default {
    name: 'Article',
    data() {
      return {
        info: {
            title:"",
            date:null,
            category_id:null,
            category_name:"",
            content:""
        }
      }
    },
    created: async function () {
      const {
        data: res
      } = await this.$http.get("Article?id=" + this.$route.params.id);
      this.info = res;
      this.info.content = this.$Markdown.render(this.info.content);
    },
  }

</script>

<style scoped>
  .meta {
    text-align: center;
  }

  .meta .meta-item+.meta-item::before {
    content: "|";
    margin: 0 0.5em;
  }

  .Article {
    background: #fff;
    width: calc(100% - 20px);
    padding: 10px;
    overflow: hidden;
  }

  .title {
    text-align: center;
    font-size: 40px;
  }

</style>



