import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Article from '@/components/Article'
import Category from '@/components/Category'

Vue.use(Router)

export default new Router({
  //mode : 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: Category
    },
    {
      path: '/Article/:id',
      name: 'Article',
      component: Article
    },
    {
      path: '/Category/:id',
      name: 'Category',
      component: Category
    }
  ]
})



