<template>
  <div class="Category">
    <el-card class="box-card" v-for="item in info" :key="item.id">
        <h1>
            <router-link :to="'/Article/'+item.id">
            {{ item.title }}
            </router-link>
            </h1>
            <div class="meta">
      <span class="meta-item">
        <span class="meta-item-icon">
          <i class="el-icon el-icon-date"></i>
        </span>
        <span class="meta-item-text"> {{ item.date }}</span>
      </span>
      <span class="meta-item">
        <span class="meta-item-icon">
          <i class="el-icon el-icon-folder"></i>
        </span>
        <span class="meta-item-text"> <router-link :to="'/Category/'+item.category_id">
            <el-link type="primary">{{ item.category_name }}</el-link>
          </router-link></span>
      </span>
    </div>
            <el-divider></el-divider>
        <div class="content" v-html="$Markdown.render(item.content).replace('<br/>','')"></div>
    </el-card>
  </div>
</template>

<script>
import '@/assets/css/markdown.css';
export default {
  name: 'Category',
  data () {
    return {
      info:null
    }
  },
  created: async function(){
    const {data:res} = await this.$http.get(!this.$route.params.id?"Category":"Category?id="+this.$route.params.id);
        //this.content = this.$Markdown.render(res.content);
        this.info=res;
  },
  watch: {
    async $route(to, from) {
         const {data:res} = await this.$http.get(!to.params.id?"Category":"Category?id="+to.params.id);
        //this.content = this.$Markdown.render(res.content);
        this.info=res;
        }
    },

  
}
</script>

<style>
  .hello{ background: #fff; width:calc(100% - 20px); padding: 10px; overflow: hidden;}
  .content{  height: 90px; overflow: hidden;}
  .content h1,.content h2,.content h3,.content h4,.content h5,.content h6,.content p,.content ul,.content ol,.content li,.content strong{line-height: 30px; font-size: 16px; font-weight: normal; margin: 0; padding: 0; }
  .meta .meta-item+.meta-item::before {
    content: "|";
    margin: 0 0.5em;
  } 
  .Category h1{margin-bottom: 10px;}
  .Category .el-card h1 a:hover{text-decoration:underline;}
  .Category .el-card{ margin-bottom: 10px;}
</style>



